.navigation-bar {
    width: fit-content;
    height: 15px;
    background-color: ghostwhite;
    border-radius: 10px;
    padding: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    box-shadow: 1px 1px 2px;
}

.navigation-bar > p {
    font-weight: bold;
    font-size: large;
    margin-left: 15px;
    margin-right: 15px;
    color: #333333;
}

.navigation-bar a {
    text-decoration: None;
}

.navigation-bar a:visited {
    color: inherit;
}

.navigation-bar a:hover {
    color: dodgerblue;
}

