.App {
  text-align: center;
  background-color: #E2E2E2;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  position: relative;
}

.App-title {
  color: #333333;
}

.App-header {
  height: 50px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 30px;
  align-items: center;
}

.App-footer {
  position: relative;
  bottom: 0px;
  width: 100%;
  height: auto;
}

.App-footer-container {
  padding: 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

div.content-container {
  max-width: 700px;
  padding: 20px;
  text-align: left;
  margin: auto;
  flex: 1;
  flex-direction: column;
}

h1, h2, h3, h4, p {
  margin: 0;
  padding: 0;
  color: #333333
}

h1 {
  margin: 10px 0px;
  font-size: 40px;
}

h2 {
  margin: 10px 0px;
  font-size: 32px;
}

p {
  font-size: 19px;
  font-weight: 500;
}

a:hover {
  color: dodgerblue;
}
